import Dropzone from 'nuxt-dropzone'
import 'nuxt-dropzone/dropzone.css'
import axios from 'axios'

export default {
  data() {
    return {
      h1: '',
      title: '',
      description: '',
      url: '',
      content: '',
      image: '',
      isValid: false,
      addLoading: false,
      dropzoneOptions: {
        url: 'https://httpbin.org/post',
        maxFiles: 1,
        maxFilesize: 2,
        addRemoveLinks: true,
        acceptedFiles: '.jpg, .jpeg, .png',
        dictDefaultMessage: `<p class='text-default'><i class='fa fa-cloud-upload mr-2'></i> Drag cover image here or click to upload</p>
          <p class="form-text">Allowed Files: .jpg, .jpeg, .png</p>
          <p class="form-text">Allowed Size: < 2Mb</p>
          `,
      },
    }
  },
  components: {
    Dropzone,
  },
  computed: {
    isDisabled: function () {
      if (
        this.h1 === '' ||
        this.title === '' ||
        this.description === '' ||
        this.url === '' ||
        this.content === '' ||
        this.image === ''
      ) {
        return !this.isValid
      }
    },
  },
  methods: {
    async afterComplete(upload) {
      this.isLoading = true
      try {
        if (upload.size < this.dropzoneOptions.maxFilesize * 1024 * 1024) {
          this.image = upload
        } else {
          swal('Error', 'Image size is to big', 'error')
        }
      } catch (error) {
        console.log(error)
        swal('Error', 'Something Went wrong', 'error')
      }
    },
    addNewArticle() {
      let formData = new FormData()
      formData.append('h1', this.h1)
      formData.append('title', this.title)
      formData.append('description', this.description)
      formData.append('url', this.url)
      formData.append('content', this.content)
      formData.append('image', this.image)
      this.addLoading = true
      this.$axios
        .post('/api/articles', formData)
        .then((response) => {
          this.addLoading = false
          swal('Success', 'Article added successfully', 'success')
          this.$router.push({ name: 'articles', params: { created: 'yes' } })
        })
        .catch((err) => {
          this.addLoading = false
          swal('Error', err.message, 'error')
        })
    },
    onRemove() {
      this.image = ''
    },
  },
}
